import React from 'react';
import axios from 'axios';

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = { channel: '', ethAddress: '' };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    const name = event.target.name
    this.setState({ [name]: event.target.value });
  }

  handleSubmit(event) {
    axios.get(`https://api.twitch.tv/kraken/users/${this.state.channel}?client_id=y8n21fwws8pnf1jhlhdv6hplclr7sl`, { 'headers': { 'client_id': 'y8n21fwws8pnf1jhlhdv6hplclr7sl', 'Access-Control-Allow-Headers': 'Origin, X-Requested-With, Content-Type, Accept, Client-ID, access-control-allow-origin', 'Access-Control-Allow-Methods': 'GET,HEAD,OPTIONS,POST,PUT' } })
      .then(res => {
        const data = res.data
        console.log(data)
        axios.get(`https://api.twitch.tv/api/channels/${this.state.channel}/panels?client_id=y8n21fwws8pnf1jhlhdv6hplclr7sl`, { 'headers': { 'client_id': 'y8n21fwws8pnf1jhlhdv6hplclr7sl', 'Access-Control-Allow-Headers': 'Origin, X-Requested-With, Content-Type, Accept, Client-ID, access-control-allow-origin', 'Access-Control-Allow-Methods': 'GET,HEAD,OPTIONS,POST,PUT' } })
          .then(res => {
            let panels = res.data
            panels.forEach(panel => {
              //TO DO check / posibility bs ass bug
              if (panel.data.link === 'https://www.reddit.com') {
                console.log('a')
                alert('YAY PANEL FOUND')
              }
            });
          }).catch((err) => {
            alert(err.response)
          })
      }).catch((err) => {
        alert(err.response)
      })

    event.preventDefault();
  }

  render() {
    return (
        <form onSubmit={this.handleSubmit}>
        <label>
          Twitch URL:
            <input type="text" name="channel" value={this.state.channel} onChange={this.handleChange} />
        </label>
        <label>
          ETH Adress:
            <input type="text" name="ethAddress" value={this.state.ethAddress} onChange={this.handleChange} />
        </label>
        <input type="submit" value="Submit" />
        </form>
    );
  }
}

export default Home;



